.MenuPage img {
  width: 4rem;
  height: 4rem;
}

#Courses {
  display: inline-flex;
}

#Course {
  display: inline-flex;
}

#Course div {
  width: 15rem;
  text-align: left;
  border-bottom: solid 0.2rem aqua;
  margin-left: 2rem;
  margin-right: 0;
  margin-top: 2rem;
  padding-right: 7rem;
}

#Course div p {
  display: inline;
  font-size: 1rem;
}

#Course div #CourseName {
  margin: 0;
  margin-right: 1rrem;
  font-size: 3rem;
}

#Course img {
  margin-top: auto;
}

.Image {
  display: inline-flex;
  margin-top: 2rem;
  margin-left: 4rem;
}

.Image img {
  width: 8rem;
  height: 8rem;
  margin-top: auto;
}

.MateCourses #Course p {
  color: blue;
}

.MateCourses #Course #CourseName {
  color: rgb(153, 188, 255);
}

.PhysicsCourses #Course p {
  color: green;
}

.PhysicsCourses #Course #CourseName {
  color: rgb(0, 255, 0);
}
