body {
    margin: 0;
    background-color: rgb(237, 237, 237);
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 2.5em;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

header {
    position: relative;
}

header div {
    display: inline-block;
    background-color: rgb(220, 255, 255);
    width: 7em;
    height: 10vh;
    margin-bottom: 0vh;
}

header img {
    width: 7em;
    height: 10vh;
    display: block;
    margin: 0;
    padding: 0;
}

header p {
    width: 7em;
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    margin: 0;
    padding: 0;
    color: whitesmoke;
    text-align: center;
}

#Left {
    position: absolute;
    top: 0;
    left: 0;
    width: 5em;
    height: 100vh;
}

#Left div img {
    width: 5em;
}

#Left div {
    position: relative;
    width: 5em;
    margin-bottom: 3vh;
}

#Left div p {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 5em;
}

#Center {
    position: absolute;
    text-align: left;
    margin-bottom: 0;
    top: 0;
    left: 5em;
    width: calc(100vw - 5em);
    height: 10vh;
}

#Center div {
    position: relative;
    margin-bottom: 0;
    margin-left: 2vh;
}

#Center div p {
    position: absolute;
    top: 50%;
    left: 50%;
}

#Left #logo {
    width: 5em;
    height: 5em;
    margin-bottom: 3vh;
}

#Left #menu {
    padding: 0;
    margin: 0;
}

#Left #apply {
    height: 5vh;
}

#Left #logo img {
    background-color: aquamarine;
    width: 5em;
    height: 5em;
}

#Left #apply img {
    background-color: rgb(229, 175, 76);
    height: 5vh;
}

#Left #apply p {
    font-size: 2rem;
}

#Left #qanda {
    margin-bottom: 0;
}

#MainContent {
    position: relative;
    left: 7em;
    top: 10vh;
    width: calc(100vw - 7em);
    height: auto;
    margin: 0;
}